import React from 'react';
import {OverlayTrigger} from 'react-bootstrap';


const renderTooltip = props => (
  <div
    {...props}
      style={{
        backgroundColor: '#fff',
        color: '#212529',
        border: '1px solid #212529',
        padding: '10px',
        margin: '0 0 20px 10px',
        borderRadius: 3,
        ...props.style,
    }}
  >
    Please applying your position
  </div>
);


const Popover = () => {
  return(
      <OverlayTrigger placement="right-start" delay={{ show: 250, hide: 400 }} overlay={renderTooltip} href="/" tabIndex="0" className="popover-link rounded-circle pl-1" id="example" role="button" data-toggle="popover" data-trigger="focus" title="Please applying your" data-content="Please select your position"><i className="fas fa-question-circle" ></i></OverlayTrigger>
  )
}

export default Popover;